import React from "react";
import * as FileSaver from "file-saver";
import * as XLSX from "xlsx";
import { RiFileExcel2Fill } from "react-icons/ri";

// type any[] is for single sheet file
// type { sheetName: string; data: any[] }[] is for multiple sheets file
export type FileMultipleDataType = { sheetName: string; data: any[] };
export type FileDataType = any[] | FileMultipleDataType[];

interface ExportToExcelProps {
  fileData: FileDataType;
  fileName: string;
  btnName?: string;
  className?: string;
  showIcon?: boolean;
}

const ExportToExcel: React.FC<ExportToExcelProps> = ({
  fileData,
  fileName,
  btnName = "Excel",
  className = "flex items-center gap-1 px-4 py-1 rounded text-green-700 bg-white border hover:text-white hover:bg-green-700 border-green-700 font-bold",
  showIcon = true,
}) => {
  const fileType =
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const fileExtension = ".xlsx";

  const exportToCSV = (excelData: FileDataType, fileName: string) => {
    if (FN_IsDataForMultiple(excelData)) {
      const workbook = XLSX.utils.book_new();
      excelData.forEach((dataItem: { sheetName: string; data: any[] }) => {
        const worksheet = XLSX.utils.json_to_sheet(dataItem.data);
        XLSX.utils.book_append_sheet(workbook, worksheet, dataItem.sheetName);
      });

      const excelBuffer = XLSX.write(workbook, {
        bookType: "xlsx",
        type: "array",
      });
      const data = new Blob([excelBuffer], { type: fileType });
      FileSaver.saveAs(data, fileName + fileExtension);
    } else {
      const worksheet = XLSX.utils.json_to_sheet(excelData);
      const workbook = { Sheets: { data: worksheet }, SheetNames: ["data"] };
      const excelBuffer = XLSX.write(workbook, {
        bookType: "xlsx",
        type: "array",
      });
      const data = new Blob([excelBuffer], { type: fileType });
      FileSaver.saveAs(data, fileName + fileExtension);
    }
  };

  return (
    <button
      onClick={(e) => exportToCSV(fileData, fileName)}
      className={className}
    >
      {showIcon && <RiFileExcel2Fill />}
      {btnName}
    </button>
  );
};

// THIS FUNCTION WILL LOOP THROUGH THE ARRAY GIVEN TO CHECK WEEVER
// THE DATA TYPE IS VALID FOR MULTIPLE SHEET
const FN_IsDataForMultiple = (
  data: { sheetName: string; data: any[] }[]
): boolean => {
  let i = 0;
  while (i < data.length) {
    if (
      data[i].sheetName === undefined ||
      typeof data[i].sheetName !== "string" ||
      data[i].data === undefined ||
      Array.isArray(data[i].data) === false
    )
      return false;
    i++;
  }
  return true;
};

export default ExportToExcel;
